/**** TRANSITION COLOR HOVER ****/

.transitionColor p {
    position: relative;
    display: block;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    white-space: nowrap;
}
  
.transitionColor p:hover {
    -webkit-transition: all 1s;
    transition: all 1s;
}
  
.transitionColor p:before {
    content: attr(data-hover);
    position: absolute;
    color: var(--pink);
    left: 0;
    width: 0;
    -webkit-transition: width 0.4s;
    transition: width 0.4s;
    overflow: hidden;
    white-space: nowrap;  
  }
  
.transitionColor p:hover:before {
    width: 100%;
    -webkit-transition: width 1s;
    transition: width 1s;
  
}
  
.navbarWrapper .navbarElements p:hover:before {
    width: 100%;
    -webkit-transition: width 0.3s;
    transition: width 0.3s;
}


/**** BURGER MENU  ****/

.burgerMenuWrapper {
    display: inline-block;
    cursor: pointer;
    width: 30px;
    display: none;
    z-index: 2;
}

.bar1, .bar2, .bar3 {
    width: 85%;
    height: 3px;
    background-color: var(--gray);
    margin: 3px auto;
    -webkit-transition: all linear 0.2s;
    transition: all linear 0.2s;
    cursor: pointer;
}



.burgerMenuWrapper:hover .bar1, .burgerMenuWrapper:hover .bar3 {
    width: 100%;
    -webkit-transition: all linear 0.2s;
    transition: all linear 0.2s;
}

.open .bar1 {
    -webkit-transform: rotate(-49deg) translate(-7px, 5px);
            transform: rotate(-49deg) translate(-7px, 5px);
}

.open .bar2 {
    opacity: 0;
}

.open .bar3 {
    -webkit-transform: rotate(45deg) translate(-3px, -2px);
            transform: rotate(45deg) translate(-3px, -2px);
}


