/*UNDERLINE TRANSITION EFFECT */
.custom-underline {
    position: relative;
    display: inline;
    display: initial;
    color: var(--black);
    padding-bottom: 5px;
    border-bottom: 1px solid var(--black);
    -webkit-transition: 0.5s ease all;
    transition: 0.5s ease all;
    font-size: 16px;
  }
  
  .custom-underline::after {
    content: "";
    position: absolute;
  }
  
  .box-link {
      text-align: center;
      margin: 30px 0px 80px;
  }
  
  .link {
    -webkit-transition: 0.3s linear all .1s;
    transition: 0.3s linear all .1s;
  }
  
  .link:hover, .custom-underline:hover {
    color: var(--gray);
    -webkit-transition: 0.3s ease all;
    transition: 0.3s ease all;
  }
  
  .link .custom-underline::after {
    top: 100%;
    height: 1px;
    width: 0%;
    left: 0;
    background-color: var(--gray);
    -webkit-transition: 0.5s linear all .1s;
    transition: 0.5s linear all .1s;
  }
  
  .link:hover .custom-underline::after {
    width: 100%;
    -webkit-transition: 0.6s ease all;
    transition: 0.6s ease all;
  }

